<h1 id="服务端" tabindex="-1"><a class="header-anchor" href="#服务端" aria-hidden="true">#</a> 服务端</h1>
<p>企业微信第三方服务端推送和公众号一样，请参考：<RouterLink to="/6.x/official-account/server.html">公众号：服务端</RouterLink></p>
<h2 id="第三方平台推送事件" tabindex="-1"><a class="header-anchor" href="#第三方平台推送事件" aria-hidden="true">#</a> 第三方平台推送事件</h2>
<p>企业微信第三方数据推送的有以下事件：</p>
<ul>
<li>suite_ticket 推送 <code>suite_ticket</code></li>
<li>授权成功 <code>create_auth</code></li>
<li>授权变更 <code>change_auth</code></li>
<li>授权取消 <code>cancel_auth</code></li>
<li>通讯录变更（Event） <code>change_contact</code>
<ul>
<li>ChangeType
<ul>
<li>成员变更
<ul>
<li>新增成员 <code>create_user</code></li>
<li>更新成员 <code>update_user</code></li>
<li>删除成员 <code>delete_user</code></li>
</ul>
</li>
<li>部门变更
<ul>
<li>新增部门 <code>create_party</code></li>
<li>更新部门 <code>update_party</code></li>
<li>删除部门 <code>delete_party</code></li>
</ul>
</li>
<li>标签变更
<ul>
<li>成员标签变更 <code>update_tag</code></li>
</ul>
</li>
</ul>
</li>
</ul>
</li>
<li>共享应用事件回调 <code>share_agent_change</code></li>
</ul>
<h2 id="自定义消息处理器" tabindex="-1"><a class="header-anchor" href="#自定义消息处理器" aria-hidden="true">#</a> 自定义消息处理器</h2>
<blockquote>
<p><em>消息处理器详细说明见：公众号开发 - 服务端一节</em></p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// 授权成功事件</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleAuthCreated</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 授权变更事件</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleAuthChanged</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 授权取消事件</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleAuthCancelled</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 通讯录变更事件</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleContactChanged</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>


<span class="token comment">// 成员变更事件</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleUserCreated</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleUserUpdated</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleUserDeleted</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 部门变更事件</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handlePartyCreated</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handlePartyUpdated</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handlePartyDeleted</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 成员标签变更事件</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleUserTagUpdated</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 共享应用事件</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleShareAgentChanged</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// suite_ticket 推送事件</span>
<span class="token variable">$server</span><span class="token operator">-></span><span class="token function">handleSuiteTicketRefreshed</span><span class="token punctuation">(</span><span class="token keyword">callable</span> <span class="token operator">|</span> <span class="token keyword type-declaration">string</span> <span class="token variable">$handler</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br></div></div><blockquote>
<p>{warning} 注意：如果你自行处理了 SuiteTicket 推送，你必须同时设置 ProviderAccessToken 类，因为 ProviderAccessToken 依赖它。</p>
